.movies {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 45px;
    padding-top: 70px;
    width: 80%;
    
}

.movies .movie {
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 3px 10px 5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    transition: all .5s;
}

.movies .movie:hover {
    transform: translateY(-10px);
}

.movie img {
    position: relative;
    top: -30px;
    max-width: 120px;
    margin-right: 15px;
    box-shadow: 0 3px 10px 5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year{
    margin: 0;
    font-weight: 300;
}

.movie .movie__title, 
.movie .movie__genres{
   color: #b2b3bc;
    
}

.movie .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}


.movie .movie__genres{
    list-style: none;
    padding: 0;
    margin: 5px 0;
    display: flex;
    
}

.movie__genres li {
    margin-right: 10px;
    font-size: 14px;
}